.custom-header {
  background-color: transparent;
  left: 0;
  width: 100%;
}

.custom-header .overlap-5 {
  background: linear-gradient(
    90deg,
    rgb(255, 0, 0) 0%,
    rgba(217, 217, 217, 0) 100%
  );
  height: 70px;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
}

.custom-header .text-wrapper-3 {
  color: var(--white);
  font-family: var(--font-family);
  font-size: 24px;
  font-weight: 600;
  letter-spacing: -0.33px;
  line-height: normal;
  white-space: nowrap;
  margin-left: 40px;
}

.custom-prevbtn.prevpagebtn {
  background-color: var(--white);
  border: 1px solid;
  border-color: var(--border-color);
  border-radius: 50%;
  width: 55px;
  padding: 12px;
}

.custom-prevbtn .arrow {
  left: 11px;
  top: 13px;
  width: 25px;
}

.custom-current {
  justify-content: center;
  border-radius: var(--radius);
  background-color: #d4ebd4;
  border: 1px solid #009900;
  height: 34px;
  width: 169px;
  color: #009900;
  font-weight: 700;
  font-size: 20px;
  letter-spacing: normal;
  margin: 50px 0 0 0;
  padding: 8px 16px;
  display: flex;
  align-items: center;
}

.custom-pastdue {
  justify-content: center;
  background-color: #fbd5b4;
  border: 1px solid #ff7700;
  width: 169px;
  color: #ff7700;
  font-weight: 700;
  font-size: 20px;
  letter-spacing: normal;
  margin: 50px 0 0 0;
  padding: 8px 16px;
  border-radius: var(--radius);
  display: flex;
  align-items: center;
  height: 34px;
}

.payment-schedule-btn,
.collateral-details-btn {
  font-size: 14px;
  padding: 8px;
  display: flex;
  align-items: center;
}

.payment-schedule-btn,
.collateral-details-btn,
.confirmation-btn,
.confirmation-cancel-btn {
  background-color: var(--red);
  font-size: 18px;
  font-weight: 500;
  border-radius: var(--radius);
  padding: 8px 12px;
  color: var(--white);
  border: none;
  cursor: pointer;
}

.confirmation-cancel-btn {
  background-color: #88888888 !important;
}

.confirmation-btn,
.confirmation-cancel-btn {
  margin: 10px;
  width: 100px;
  height: 35px;
}
.collateral-details-btn {
  margin-left: 35px;
}

.payment-schedule-btn .download-icon,
.collateral-details-btn .download-icon {
  margin: auto 3px;
}

.btn-enabled {
  background-color: var(--red);
  color: var(--white);
  padding: 4px 10px;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: var(--radius);
  width: 320px;
  height: 50px;
}

.btn-disabled {
  background-color: var(--disable);
  color: var(--white);
  padding: 4px 10px;
  font-size: 20px;
  display: flex;
  cursor: no-drop;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: var(--radius);
  width: 320px;
  height: 50px;
}

.custom-card-title {
  color: var(--text-red);
  max-width: 700px;
  margin: 0 auto;
  padding-bottom: 15px;
  padding-top: 20px;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.011em;
}

.inputs {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.loan-input-label,
.loan-input-sublabel {
  margin: 0 0 0 10px;
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 0em;
  text-align: left;
  color: var(--border-color-gray);
}

.loan-input-sublabel {
  margin-top: 4px;
  font-size: 10px !important;
  font-style: italic !important;
}

.loan-input-wrapper input {
  height: 32px;
  padding: 10px;
  border-radius: var(--radius);
  margin-right: 10px;
  border: 1px solid;
  border-color: var(--border-color-gray);
  font-size: 18px;
  color: var(--text-gray);
  font-weight: 400;
  line-height: 30px;
  text-align: right;
}

.loan-input-wrapper input:focus {
  border-color: var(--border-color);
  outline: none;
}

.custom-card-subtitle {
  display: block;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0em;
  color: var(--text-gray);
}

.loan-input-wrapper input::placeholder {
  color: var(--text-gray);
  font-size: 16px;
  font-family: var(--font-family);
  font-weight: 400;
}

.modalBackground {
  background: var(--bg-shadow);
  display: flex;
  margin: 0 auto;
  width: 100%;
}

.modal-content {
  font-family: var(--font-family);
}
.custom.modal-content{
  max-width: 400px;
}

.modal-body {
  display: grid;
  text-align: center;
  background-color: var(--white);
}

.loan-details .modal-content{
  max-width: 390px;
}
.loan-details .modal-body{
  text-align: justify;
  padding: 30px 30px 15px 30px;
  border-radius: var(--radius);
}
.loan-details .modal-actions .confirmation-btn,
.loan-details .modal-actions .confirmation-cancel-btn{
  border-radius: var(--radius);
}
.loan-details .modal-actions{
  text-align: center;
}
.loan-details .modal-title{
  font-size: 20px;
  font-weight: 600;
  margin: 0px 0px 20px 0px;
}

#title-alert {
  font-weight: 700;
  font-size: 19px;
}

#text-alert {
  margin: 20px;
  line-height: 23px;
  word-wrap: break-word;
  font-size: 17px;
}
#Ok-btn {
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  height: 40px;
  background-color: var(--red);
  color: var(--white);
  font-weight: 100px;
  border-radius: var(--radius);
  border-color: var(--border-color);
}

#sub-btn{
  width: 230px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  height: 40px;
  background-color: var(--red);
  color: var(--white);
  font-weight: 100px;
  border-radius: var(--radius);
  border-color: var(--border-color);
  font-size: 15px;
  cursor: pointer;
}

/* LOADING */
.modal-loading--wrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg-shadow);
  z-index: 999;
}

.modal-loading--container{
  width: 330px;
  /* height: 150px; */
  background-color: white;
  text-align: center;
  padding: 10px;
  margin: 20px auto;
  border-radius: var(--radius);
}
.modal-loading--text{
  font-weight: 600;
  font-size: 17px;
}
.modal-loading--content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px 0px;
  align-items: center;
  margin: 10px auto;
}
.modal-loading--circle,
.modal-loading--text{
  margin: 10px 0px;
}

.modal-loading--circle{
  display: flex;
  justify-content: center;
}
.custom-confirm.modal .modal-content .modal-body{
  border-radius: var(--radius);
  text-align: justify;
}
.custom-confirm.modal .modal-content{
  width: 400px;
}
.custom-confirm.modal .modal-description{
  margin: 10px 0;
}
.custom-confirm.modal .modal-actions{
  text-align: end;
  margin: 10px 0px 0px 0px;
}

@media screen and (min-width: 360px) and (max-width: 480px) {
  .custom-current,
  .custom-pastdue {
    height: 16px;
    width: 94px;
    margin: 15px 10px 0 10px;
    padding: 10px 16px;
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: -0.3px;
  }

  .payment-schedule-btn,
  .collateral-details-btn {
    width: 140px;
    height: 31px;
    font-size: 12px;
    padding: 4px;
  }

  .collateral-details-btn {
    margin-left: 20px;
  }

  .loan-input-label,
  .loan-input-sublabel {
    font-size: 16px;
    margin: 0;
  }

  .loan-input-wrapper input {
    width: 180px;
  }

  .custom-card-subtitle {
    font-size: 14px;
  }
  .custom-confirm.modal .modal-actions{
    text-align: center;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .custom-current,
  .custom-pastdue {
    height: 16px;
    width: 94px;
    margin: 15px 10px 0 10px;
    padding: 10px 16px;
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: -0.3px;
  }

  .payment-schedule-btn,
  .collateral-details-btn {
    width: 140px;
    height: 31px;
    font-size: 12px;
    padding: 4px;
  }

  .collateral-details-btn {
    margin-left: 20px;
  }

  .loan-input-label,
  .loan-input-sublabel {
    font-size: 16px;
    margin: 0;
  }

  .loan-input-wrapper input {
    width: 180px;
  }

  .custom-card-subtitle {
    font-size: 14px;
  }
}
