.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg-shadow);
  z-index: 999;
}

.modal-content {
  overflow: hidden;
  min-width: 360px;
  max-width: 480px;
}

.modal-header h2 {
  color: var(--white);
  font-size: 32px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: -0.3333333432674408px;
  text-align: center;
}

.modal-header span {
  color: var(--white);
  display: flex;
  justify-content: end;
  font-size: 32px;
}

.modal-body {
  background-color: var(--white);
  padding: 30px;
  border-radius: 25px;
  border: 2px;
}

.guide-image-container {
  text-align: center;
  position: relative;
}

.guide-image {
  max-width: 300px;
  max-height: 160px;
  margin: 0 auto;
  display: block;
}

.uploaded-image {
  max-width: 300px;
  margin: 0 auto;
  width: 100%;
  display: none;
}

.add-photo-text {
  text-align: center;
  margin-top: 40px;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: -0.3333333432674408px;
  cursor: pointer;
}

input[type="file"] {
  display: block;
  margin: 0 auto;
  margin-top: 10px;
}

.modal-footer {
  text-align: center;
  padding: 16px;
}

.modal-footer button {
  color: var(--white);
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: -0.3333333432674408px;
  width: 276px;
  height: 38px;
  border-radius: 41px;
  background: var(--red);
  border: none;
  cursor: pointer;
}

.modal-footer button:disabled {
  background-color: gainsboro;
  cursor: no-drop;
}

.modal-close {
  cursor: pointer;
}

.question-mark-icon {
  margin-bottom: 10px;
}

.question-mark-icon::before {
  content: "?";
  width: 28px;
  height: 27px;
  display: inline-block;
  background-color: var(--white);
  color: #000;
  border: 1px solid #000;
  font-size: 18px;
  font-weight: 900;
  text-align: center;
  line-height: 28px;
  border-radius: 50%;
  margin-right: 10px;
}

.modal-title {
  font-size: 24px;
  font-weight: bold;
  color: #000;
  margin-bottom: 16px;
}

.modal-description {
  font-size: 18px;
  color: var(--text-gray);
  margin-bottom: 24px;
}

.modal-button {
  padding: 8px 16px;
  border-radius: var(--radius);
  background-color: var(--red);
  color: var(--white);
  font-size: 16px;
  cursor: pointer;
  border: none;
}

@media screen and (min-width: 360px) and (max-width: 480px) {
  .modal-content {
    padding: 20px;
  }

  .add-photo-text {
    margin-top: 20px;
  }

  .modal-header h2 {
    font-size: 24px;
    font-weight: 500;
  }
}
